<template>
  <div class="user-detail">
    <div class="base-info">
      <h3 class="title">基本信息</h3>
      <div class="content-body">
        <div>
          <span>姓名: </span>
          <span>{{ info.user && info.user.username }}</span>
        </div>
        <div>
          <span>帐号: </span>
          <span>{{ info.user && info.user.account }}</span>
        </div>
        <div>
          <span>角色: </span>
          <gs-tag>{{ info.role && info.role.roleName }}</gs-tag>
        </div>
      </div>
    </div>
    <div class="auth-info">
      <h3 class="title">权限信息</h3>
      <div class="content-body">
        <div class="left">
          <div class="authorize-module">
            <div class="title">授权模块</div>
            <p
              v-for="item in info.modules.filter(i => i.type) || []"
              :key="item.moduleId"
            >
              <i class="gs-icon-check" />{{ item.moduleName }}
            </p>
          </div>
          <div class="business-obj-ope">
            <div class="title">业务对象操作</div>
            <p
              v-for="item in info.modules.filter(i => !i.type) || []"
              :key="item.moduleId"
            >
              <i class="gs-icon-check" />{{ item.moduleName }}
            </p>
          </div>
        </div>
        <div class="right">
          <div class="title">授权范围</div>
          <div class="sub-title">技术目录</div>
          <gs-table
            :data="info.technicalCategory || []"
            style="width: 100%"
          >
            <gs-table-column
              prop="categoryName"
              show-overflow-tooltip
              label="目录名称"
            />
            <gs-table-column
              prop="level"
              show-overflow-tooltip
              label="目录层级"
            />
            <gs-table-column
              prop="level2Category"
              show-overflow-tooltip
              label="二级目录名称"
            />
          </gs-table>
          <div class="sub-title">业务目录</div>
          <gs-table
            :data="info.businessCategory || []"
            style="width: 100%"
          >
            <gs-table-column
              prop="categoryName"
              show-overflow-tooltip
              label="目录名称"
            />
            <gs-table-column
              prop="level"
              show-overflow-tooltip
              label="目录层级"
            />
            <gs-table-column
              prop="level2Category"
              show-overflow-tooltip
              label="二级目录名称"
            />
          </gs-table>
        </div>
      </div>
    </div>
    <gs-loading :is-show="loading" />
  </div>
</template>
<script>
import * as service from 'src/service/auth/user';
export default {
  data() {
    return {
      info: {
        user: {},
        role: [],
        modules: []
      },
      loading: false
    };
  },
  created() {
    this.fetchUserDetail();
  },
  methods: {
    fetchUserDetail() {
      const userId = this.$route.params.id;
      this.loading = true;
      service.getUserDetail(userId).then(data => {
        this.info = data;
        if (data.role && data.role.roleId === '1') {
          this.info.technicalCategory =
            this.info.businessCategory =
          [
            {
              categoryName: '全部目录',
              level: '-',
              level2Category: '-'
            }
          ];
        }
        this.loading = false;
      }).catch(e => {
        this.loading = true;
      });
    }
  }
};
</script>




